
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import scroll from "../scroll";

import { mapState, mapGetters } from "vuex";
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
Popup,
Checker,
CheckerItem,
Datetime,

XButton,
scroll

},
computed: {
...mapGetters([
"demoPopup",
"demosList",
"demosTotal",
"demosPerson",
"demoScrollTop",
"demoSelect"
]),
pullDownRefreshObj: function() {
return this.pullDownRefresh
? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop),
text: "刷新完成"
}
: false;
},
pullUpLoadObj: function() {
return this.pullUpLoad
? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {
more: this.pullUpLoadMoreTxt,
noMore: this.pullUpLoadNoMoreTxt
}
}
: false;
}
},
data() {
return {
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullDownRefreshText: "刷新完成",
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: "加载更多",
pullUpLoadNoMoreTxt: "没有更多数据了",
startY: 0,
scrollToX: 0,
scrollToY: -200,
scrollToTime: 700,
scrollToEasing: "bounce",
scrollToEasingOptions: ["bounce", "swipe", "swipeBounce"],
page: 1,
num: 10,



demos: [],
lessons: [],
isShow: false,
selectedDate: { key: 1, value: "本年" },
dateClass: [
{
key: 1,
value: "本年"
},
{
key: 2,
value: "去年"
}
],
years: "",
defyear: ""
};
},
created() {
this.defyear = `${new Date().getFullYear()}`;
if (this.$route.query.status === "dynamic") {
this.$store.dispatch("getDemosList");
} else if (this.$route.query.status === "statistics") {
this.$store.dispatch("getDemosPerson");
}
},
mounted() {






},
activated() {
this.demos = this.demosList;
this.lessons = this.demosPerson;






},
beforeDestroy() {

},
watch: {
demoPopup: function(val) {
this.isShow = val;
},
demosList: function(val) {
this.demos = val;
},
demosPerson: function(val) {
this.lessons = val;
},
demoSelect: function(val) {
this.lessons = val;
},
pullDownRefreshObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullDown();
} else {
scroll.closePullDown();
}
},
deep: true
},
pullUpLoadObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullUp();
} else {
scroll.closePullUp();
}
},
deep: true
},
startY() {
this.rebuildScroll();
},











years: function(val) {
if (val) {
this.selectedDate = {};
}
},
selectedDate: function(val) {
if (Object.keys(val).length) {
this.years = "";
}
},
$route(to, from) {
if (to.query.status === "dynamic" && to.path == "/demonstrate") {
if (this.demosList.length == 0) {
this.$store.dispatch("getDemosList");
}



} else if (
to.query.status === "statistics" &&
to.path == "/demonstrate"
) {
this.$store.dispatch("getDemosPerson");
this.box && this.box.removeEventListener("scroll", this.handler, false);
}
}
},
methods: {
scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime,
ease[this.scrollToEasing]
);
},
onPullingDown() {
console.log("pulling down and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
this.$store.dispatch("getDemosList");
this.$refs.scroll.forceUpdate();
this.page = 1;
}, 2000);
},
onPullingUp() {
console.log("pulling up and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
this.page++;
this.$store.dispatch("getDemosList", { page: this.page }).then(() => {
if (this.demos.length >= this.demosTotal) {
this.$refs.scroll.forceUpdate();
}
});
}, 1500);
},

rebuildScroll() {
Vue.nextTick(() => {
this.$refs.scroll.destroy();
this.$refs.scroll.initScroll();
});
},






refresh(done) {
this.$store.dispatch("getDemosList");
this.page = 1;
done();
},
infinite(done) {
this.page++;
let end = this.num * this.page;
let i = end - this.num;
let more = this.$el.querySelector(".load-more");
this.$store.dispatch("getDemosList", { page: this.page }).then(() => {
for (i; i < end; i++) {
if (end >= this.demosTotal) {
more.style.display = "none"; 

break;
} else {
more.style.display = "none"; 
}
}
});
done();
},
hidePopup() {
this.$store.dispatch("updateDemoPopup", {
demoPopup: false
});
},
defyears() {
return new Date().getFullYear();
},
choosedData() {
this.$store.dispatch("updateDemoPopup", {
demoPopup: false
});

if (this.years) {
this.$store.dispatch("getDemoSelect", { time: this.years });
} else {
this.years = new Date().getFullYear();
this.$store.dispatch("getDemoSelect", { time: this.years });
}
console.log(this.years);

this.years = "";
}
}
};



